<script>
	import { groupCollapsed } from "console";
	import Question from './Question.svelte';
	import Answer from './Answer.svelte';
	
	let currRank = 0;
	export let ranks = [
		{ 
			name: "Porcelain",
			class: "tag small"
		},
		{ 
			name: "Obsidian",
			class: "tag small"
		},
		{ 
			name: "Steel",
			class: "tag small"
		},
		{ 
			name: "Sapphire",
			class: "tag small"
		},
		{ 
			name: "Emerald",
			class: "tag small"
		},
		{ 
			name: "Ruby",
			class: "tag small"
		},
		{ 
			name: "Bronze",
			class: "tag small"
		},
		{ 
			name: "Silver",
			class: "tag small"
		},
		{ 
			name: "Gold",
			class: "tag small"
		},
		{ 
			name: "Platinum",
			class: "tag large"
		}
	]
	let quizIdx = 0;

	let rankTags = document.getElementsByClassName('tag');
	

	const quiz = [
		{
			question: 'What is the standard abbreviation for your Charisma score?',
			correctAnswer: 'CHA',
			answers: [
				'CHA',
				'DEX',
				'WIS',
				'CON'
			],
		},
		{
			question: 'This is question 2',
			correctAnswer: 1,
			answers: [
				'A',
				'B',
				'C',
				'D'
			],
		}
	]

	function checkAnswerHandler(answerText, id){
		const isCorrect = answerText === quiz[quizIdx].correctAnswer;
		if (isCorrect) {
			document.getElementById(id).classList.add('green');
			currRank += 1;
			quiz[quizIdx].question = 'Correct!';
			setTimeout(() => {
				document.getElementById(id).classList.remove('green');
				quizIdx += 1;
			}, 2000)
		}
		else {
			quiz[quizIdx].question = 'Wrong!';
		}
	}
</script>

<main>
	<div class="top">
		<div class="title">Who Wants To Be A Platinum-Rank Adventurer?</div>
		<div class="board">
			{#each ranks as rank, id}
				<div class="tag-area">
					{#if id === currRank}
					<div class="tag-contain curr"><div class={`${rank.class} curr-font`}>{rank.name}</div></div>
					{:else}
					<div class="tag-contain"><div class={rank.class}>{rank.name}</div></div>
					{/if}
				</div>
			{/each}
		</div>
	</div>
	<Question questionText={quiz[quizIdx].question}/>
	<div class="answers">
		{#each quiz[quizIdx].answers as answer, idx }
		<button class="answer" id={idx} on:click on:click={checkAnswerHandler(answer, idx)} >
			<Answer answerText={answer} {idx} />
		</button>
		{/each}	
	</div>
</main>

